@media (min-width: 768px){

.navbar {
  margin-top:0%;
}

.nav-button1 {  
    display:none; 
  }

  .horizontal-menu{
    display:flex;
    justify-content: space-between;
  }

  .right-menu{
    padding: 4%;
    margin-right:3%; 
    font-size:1.5em; 
    color: black;
    text-decoration: none;  
  }

  .left-menu{
    padding: 4%;  
    margin-left:3%;
    font-size:1.5em;
    color: black;
    text-decoration: none; 
  }
}


/* //////// MOBILE ///////// */

@media (max-width: 768px){
  .navbar, .navbar-black {
    padding-top:10%;
  }
  
  .horizontal-menu{
    display:none;
  }

  .nav-button1 {  
    display:block;
    margin: auto;
  }
  
  .logo-white-image {
    width:50%;
  }
  .navbar-black {
    background-color: black;  
  }
}

